
import Scroller from 'vux/src/components/scroller/index.vue'


export default{
name:'tags',
data () {
return{
id:0
}
},
props:{
tagList:{
type:Array
}
},
methods:{
handle(index){
this.id=index;
}
},
components:{
Scroller
},
computed:{
num(){
return this.tagList.length*0.8+'rem';
}
}
}



